.custom-form {
    @import './mixin/custom-form.scss';

    @include label;
    @include texteditor;
    @include select-box;
    @include radiogroup;
    @include htmleditor;
    @include checkbox;

    &.create-form {
        @include create-form;
    }

    &.custom-form--edit {
        padding: 16px 40px;
        @include edit;
    }

    &.custom-form--edit-notification {
        @include edit-notification;
    }

    &.custom-form--edit-readonly {
        @include edit-readonly;
    }

}

.bottom-toolbar-btns {
    display: flex;
    gap: 24px;
}