/* You can add global styles to this file, and also import other style files */
@import './style/grid.scss';
@import './style/form.scss';
@import './style/paper.scss';
@import "./style/button.scss";
@import "./style/font.scss";
@import './style/popup.scss';

:root .lpx-theme-dark {
  --lpx-logo: url('/assets/images/logo/logo-light.svg');
  --lpx-logo-icon: url('/assets/images/logo/icon.svg');
}

:root .lpx-theme-light {
  --lpx-logo: url('/assets/images/logo/logo-dark.svg');
  --lpx-logo-icon: url('/assets/images/logo/icon.svg');
}

:root {
  --lpx-theme-light-bg: url('/assets/images/login/login-bg-img-light.svg');
  --lpx-theme-dim-bg: url('/assets/images/login/login-bg-img-dim.svg');
  --lpx-theme-dark-bg: url('/assets/images/login/login-bg-img-dark.svg');

  --lpx-content-background: #F7F5E7;
  --black-1: #333;
  --white-1: #fff;
  --gray-1: #757575;
  --gray-2: #999;
  --gray-3: #ddd;
  --gray-4: #f8f8f8;
  --gray-5: #d9d9d9;
  --blue-1: #79acff;
  --blue-2: #dfe8ff;
  // --blue-3: #;
  --blue-4: #677ed9;
  --yellow-1: #ffcd63;
  --red-1: #ff6c72;
  --shadow-1: rgba(0, 0, 0, 0.1);
  --letter-1: 0.7px;
  --letter-2: 1.4px;
}



#user-organization-units {
  display: none;
}

// .form-select.form-control:disabled {
//   opacity: 0.5;
// }

.confirmation {
  z-index: 2060 !important;
}

.lpx-content> :not(abp-page-alert-container, router-outlet) {
  height: calc(100vh - 64px);
  display: block;
}

