hr {
  margin: 0.5rem 0;
}

.lpx-content-container {
  background-color: var(--lpx-content-background);
  overflow-y: scroll;

  .lpx-topbar-container .lpx-topbar {
    background-color: var(--lpx-content-background);
  }

  .lpx-content-wrapper {
    border: none;

    .lpx-content {
      padding: 0 40px;

      padding-top: 12px;
    }
  }
}


@import './style/mixin/custom-control-item.scss';
@include custom-control-item;

@import './style/mixin/custom-label.scss';
@include custom-label;