@mixin jb-toolbar-button {
    .jb-toolbar-button {
        width: 32px;
        height: 32px;
        border: 1px solid var(--gray-3);

        // dx-button50%
        &.dx-button.dx-button-has-icon:not(.dx-button-has-text):not(.dx-shape-standard) {
            border-radius: 8px;
        }

        .dx-button-content {
            padding: 0;
        }

        &.jb-toolbar-button--add {
            border: 0;
            background-color: var(--blue-1);
        }

        &.jb-toolbar-button--column-chooser {
            width: 56px;
            border-radius: 8px;
            box-shadow: none;

            .dx-button-content {
                gap: 8px;

                span:nth-child(3) {
                    color: var(--blue-1);
                }
            }
        }

        &.jb-toolbar-button--density {
            width: fit-content;
            height: 24px;
            box-shadow: none;
            border: none;
            background-color: transparent;

            span {
                color: var(--gray-1);
                margin-left: 8px;
            }
        }
    }
}