@mixin custom-control-item {

    .custom-text-box,
    .custom-number-box,
    .custom-select-box,
    .custom-date-box {
        &.dx-editor-filled::after {
            border-bottom: 0;
        }

        .dx-texteditor-container {
            border: 1px solid var(--gray-2);
            border-radius: 8px;
            background-color: var(--white-1);

            .dx-texteditor-input-container {
                padding: 11.5px 16px;

                .dx-texteditor-input {
                    padding: 0;
                }
            }
        }
    }

    .custom-select-box {
        padding-bottom: 1px;

        .dx-texteditor-container .dx-texteditor-buttons-container:last-child .dx-dropdowneditor-button:last-child {
            margin-inline-end: 16px;

            .dx-dropdowneditor-icon::before {
                content: url('../../assets/icons/buttons/dropdown-arrow-down.svg');
            }
        }
    }

    .custom-check-box {
        border-radius: 4px;

        .dx-checkbox-icon {
            border: 1px solid var(--gray-3);
            border-radius: 4px;
            color: var(--white-1);
            background-color: var(--white-1);
        }

        &[aria-checked=true] .dx-checkbox-icon {
            border: none;
            background-color: var(--blue-1);
        }

        &[aria-disabled=true] {
            border: none;
        }

        &[aria-disabled=true][aria-checked=true] {
            opacity: 1;

            .dx-checkbox-icon {
                background-color: var(--blue-1);
            }
        }
    }

    .custom-button {
        width: 120px;
        height: 40px;
        padding: 12px 16px;
        border: none;
        border-radius: 8px;
        color: var(--black-1);
        font-size: 16px;
        letter-spacing: var(--letter-2);

        &--blue {
            color: var(--white-1);
            background-color: var(--blue-1);
        }

        &--cancel {
            border: 1px solid var(--gray-3);
            background-color: var(--white-1);
            color: var(--black-1);
        }
    }

    .custom-button-icon .dx-icon {
        width: 24px;
        height: 24px;
    }

    .custom-tree-view {
        .dx-treeview-item {
            font-size: 16px;
            line-height: 16px;
            letter-spacing: var(--letter-1);
            color: var(--black-1);
        }

        .dx-treeview-toggle-item-visibility::before {
            content: url('../../assets/icons/buttons/chevron-right-solid.svg');
        }

        .dx-treeview-toggle-item-visibility.dx-treeview-toggle-item-visibility-opened::before {
            content: url('../../assets/icons/buttons/chevron-down-solid.svg');
        }

        .dx-state-selected>.dx-treeview-item {
            background-color: transparent;
            color: var(--blue-4);
        }
    }

    .custom-tag-box-drop-down .dx-list:not(.dx-list-select-decorator-enabled) .dx-list-item {

        // tag box
        &.dx-list-item-selected {
            background-color: transparent;
        }

        // padding
        .dx-template-wrapper.dx-item-content.dx-list-item-content {
            padding: 0;
        }

        // 
        .custom-item {
            padding: 10px;

            &--selected {
                background-color: rgba(0, 0, 0, 0.12);
            }
        }
    }
}