@mixin create-form {
    >.dx-layout-manager>.dx-widget.dx-collection>.dx-box-flex.dx-box.dx-widget.dx-collection>.dx-item.dx-box-item {
        margin-bottom: 16px;
    }

    .CronExpressionGroup .dx-form-group-caption {
        color: var(--blue-4);
    }

    .dx-form-group-caption {
        color: var(--yellow-1);
        font-size: 18px;
        font-weight: 600;
    }

    .dx-form-group-content {
        border-top: 0;
        padding: 0;
        margin-top: 16px;

        .dx-layout-manager .dx-field-item {
            margin-bottom: 16px;
            padding: 0;

            &.dx-field-item:not(.dx-first-col) {
                padding-inline-start: 8px;
            }

            &.dx-field-item:not(.dx-last-col) {
                padding-inline-end: 8px;
            }

            &:not(.dx-first-row) {
                padding-top: 0;
            }

            &.expectedEndTime,
            &.expectedPushCount {
                margin-bottom: 0;
            }

            &.isSendToAllReceivers {
                margin-bottom: 0;

                dx-radio-group {
                    padding: 11.5px 0;
                }
            }

            &.ReceiversGroup {
                margin-bottom: 0;

                .dx-layout-manager .dx-field-item {
                    margin-bottom: 0;

                    .notification-tag-box {
                        margin-top: 8px;
                    }
                }

                .dx-form-group-content {
                    margin-top: 0;
                }
            }

            &.isIndependentNotificationSettingUsing {
                margin-bottom: 8px;
            }

            &.notificationSettings {
                margin-bottom: 0;
            }

            &.notificationSchedulePlans {
                margin-bottom: 0;
            }

            &.isUsingTemplate dx-radio-group {
                padding-top: 0;
            }

            &.CustomContentGroup {
                margin-bottom: 0;

                .dx-form-group-content {
                    margin-top: 0;
                }

                &--inactive {
                    display: none;
                }
            }

            &.NotificationTemplateGroup {
                margin-bottom: 0;

                .dx-form-group-content {
                    margin-top: 0;
                }

                &--inactive {
                    display: none;
                }
            }

            &.textContentNotificationTemplateId {
                margin-bottom: 0;
            }

            &.EndpointGroup {
                margin-bottom: 0;

                .dx-form-group-content {
                    margin-top: 0;
                }

                .dx-layout-manager .dx-field-item {
                    margin-bottom: 0;
                }
            }
        }
    }

    .CronExpressionGroup,
    .BasicGroup,
    .SettingGroup,
    .SchedulePlanGroup,
    .NotificationContentGroup {
        background-color: var(--white-1);
        border-radius: 8px;
        padding: 16px 16px 24px 16px;
        box-shadow: 4px 4px 12px var(--shadow-1);
    }
}

// layout
@mixin edit {
    .dx-item.dx-box-item {

        &:last-child {
            margin-bottom: 0;
        }

        .dx-field-item {
            padding: 0;
            margin-bottom: 16px;

            &.dx-last-row {
                margin-bottom: 0;
            }

            &:not(.dx-field-item-has-group):not(.dx-field-item-has-tabs):not(.dx-first-row):not(.dx-label-v-align) {
                padding: 0;
            }

            &:not(.dx-first-col) {
                padding-inline-start: 8px
            }

            &:not(.dx-last-col) {
                padding-inline-end: 8px
            }
        }

        .dx-field-empty-item {
            padding-inline-start: 8px;
            cursor: default;
        }
    }
}

// layout
@mixin edit-notification {

    // Form
    &.custom-form--edit-notification {
        padding: 16px 24px;

        // Group
        >.dx-layout-manager>.dx-widget.dx-collection>.dx-box-flex.dx-box.dx-widget.dx-collection>.dx-item.dx-box-item:not(:last-child) {
            margin-bottom: 8px;
        }

        // Group
        .dx-form-group-with-caption.dx-form-group {
            padding: 8px;

            // Caption
            .dx-form-group-caption {
                font-size: 18px;
                font-weight: 600;
                color: var(--yellow-1);
            }

            >.dx-form-group-content {
                border-top: none;
                margin-top: 16px;
                padding: 0;
            }
        }

        // TopGroup
        .TopGroup .dx-item.dx-box-item {

            // 
            &:first-child .dx-field-item:not(.dx-last-col) {
                padding-inline-end: 0;
                height: 100%;

                >.dx-field-item-content {
                    height: 100%;
                    padding-right: 8px;
                    border-right: 0.5px solid var(--gray-5);
                }
            }

            // 
            &:last-child .dx-field-item:not(.dx-first-col) {
                padding-inline-start: 0;
                height: 100%;

                >.dx-field-item-content {
                    height: 100%;
                    padding-left: 8px;
                    border-left: 0.5px solid var(--gray-5);
                }
            }
        }

        // 
        .BasicGroup .isSendToAllReceivers {
            margin-bottom: 8px;
        }

        // 
        .notification-setting-items {
            margin-top: -8px;

            dx-radio-group {
                padding: 0;
            }
        }

        // 
        .NotificationContentGroup .dx-form-group-with-caption.dx-form-group>.dx-form-group-content {
            margin-top: 8px;

            .dx-item-content.dx-box-item-content .dx-item.dx-box-item {
                margin-bottom: 0;
            }

            // id
            .textContentNotificationTemplateId {
                margin-bottom: 0;
            }
        }

        .SchedulePlanGroup {
            padding-top: 0;
        }

        .CronExpressionGroup {
            .dx-form-group-with-caption.dx-form-group .dx-form-group-caption {
                color: var(--blue-4);
            }
        }

        @include notification-template-group;
    }
}

// 
@mixin edit-readonly {
    .BasicGroup dx-tag-box {
        display: none;
    }

    .BasicGroup,
    .SettingGroup,
    .NotificationContentGroup {
        pointer-events: none;
    }

    .dx-state-disabled {
        opacity: 1;
    }

    .dx-htmleditor.dx-htmleditor-outlined.dx-state-disabled {
        box-shadow: none;
    }
}

// 
@mixin label {
    .dx-field-item-label.dx-field-item-label-location-top {
        padding: 0;
        margin-bottom: 8px;
        pointer-events: none;

        .dx-field-item-label-content {
            display: flex;
            flex-direction: row-reverse;
            justify-content: flex-end;

            .dx-field-item-required-mark {
                color: var(--red-1);
            }
        }
    }
}

// 
@mixin texteditor {
    .dx-texteditor {
        &.dx-editor-filled {
            background-color: transparent;

            &::after {
                border-bottom: 0;
            }
        }

        &.dx-state-focused::before {
            border-bottom: 0;
        }

        .dx-texteditor-container {
            border: 1px solid var(--gray-2);
            border-radius: 8px;
            background-color: var(--white-1);

            .dx-texteditor-input-container {
                padding: 11.5px 16px;

                .dx-texteditor-input {
                    padding: 0;
                }
            }
        }
    }
}

// 
@mixin select-box {
    .dx-selectbox {
        .dx-texteditor-container .dx-texteditor-buttons-container:last-child .dx-dropdowneditor-button:last-child {
            margin-inline-end: 16px;

            .dx-dropdowneditor-icon::before {
                content: url('../../assets/icons/buttons/dropdown-arrow-down.svg');
            }
        }

        &.dx-dropdowneditor-active .dx-dropdowneditor-button:last-child .dx-dropdowneditor-icon::before {
            transform: rotate(180deg);
        }

        .dx-tag-container {
            .dx-tag {
                display: none;
            }

            .dx-placeholder {
                &::before {
                    padding: 11.5px 16px !important;
                }

                &.dx-state-invisible {
                    display: block !important;
                }
            }
        }
    }
}

// 
@mixin notification-template-group {

    .NotificationTemplateGroup--inactive,
    .CustomContentGroup--inactive {
        display: none;
    }

    .remove-group {
        height: 0;
        visibility: hidden;
    }
}

@mixin htmleditor {
    .dx-htmleditor {
        border: 1px solid var(--gray-2);
        border-radius: 8px;
        box-shadow: none;

        .dx-htmleditor-toolbar-wrapper {
            padding: 0 16px;
            border-bottom: 0;

            .dx-htmleditor-toolbar {
                padding: 0;

                .dx-toolbar-items-container {
                    padding: 8px;
                    height: fit-content;
                    border-bottom: 1px solid var(--gray-3);

                    >.dx-item {
                        padding-right: 8px;
                        height: 28px;

                        >.dx-item-content {
                            padding: 0;

                            &.dx-htmleditor-toolbar-separator {
                                width: 1px;
                                background-color: var(--gray-3);
                            }

                            >div {
                                &.dx-button {
                                    border-radius: 0;

                                    &.dx-state-hover,
                                    &.dx-state-selected,
                                    &.dx-state-active {
                                        background-color: var(--blue-2);
                                    }
                                }

                                &.dx-texteditor {
                                    width: 100%;

                                    .dx-texteditor-container {
                                        border: none;
                                        width: 100%;

                                        .dx-texteditor-input-container {
                                            padding: 0;

                                            .dx-placeholder::before {
                                                letter-spacing: var(--letter-1);
                                                padding: 0;
                                            }
                                        }
                                    }
                                }
                            }
                        }

                        &:nth-child(4),
                        &:nth-child(5),
                        &:nth-child(20) {
                            width: 128px;
                        }

                        &:nth-child(4) {
                            padding-left: 5px;
                        }

                        &:nth-child(5) {
                            padding-right: 1px;
                        }
                    }
                }
            }
        }
    }
}

@mixin radiogroup {
    .dx-radiogroup.dx-radiogroup-horizontal {

        padding: 11.5px 0;

        .dx-radiobutton {
            // margin: 0;

            .dx-radiobutton-icon {
                border: 1px solid var(--gray-3);
            }

            &.dx-item-selected .dx-radiobutton-icon {
                border: 1px solid var(--blue-1);

                &-dot {
                    background-color: var(--blue-1);
                }
            }
        }
    }

    .BasicGroup .dx-item.dx-box-item:nth-last-child(2) .dx-radiogroup.dx-radiogroup-horizontal,
    .BasicGroup .dx-item.dx-box-item:nth-last-child(3) .dx-radiogroup.dx-radiogroup-horizontal {
        &[ng-reflect-value=true] {
            padding-bottom: 0;
        }

        &[ng-reflect-value=false] {
            padding-bottom: 11.5px;
        }
    }
}

@mixin checkbox {
    .basic-form-checkbox {
        height: 100%;
        display: flex;
        flex-direction: row-reverse;
        justify-content: flex-end;
        align-items: center;
        gap: 8px;

        .dx-field-item-label.dx-field-item-label-location-top {
            margin-bottom: 0;
            position: relative;
            top: 11.5px;
        }

        .dx-field-item-content.dx-field-item-content-location-bottom {
            position: relative;
            top: 9.5px;

            .dx-checkbox-icon {
                border: 1px solid var(--gray-3);
                border-radius: 4px;
                color: var(--white-1);

                &::after {
                    display: none;
                }
            }

            >div[aria-checked="true"] .dx-checkbox-icon {
                border: none;
                color: var(--white-1);
                background-color: var(--blue-1);
            }
        }
    }
}