@mixin basic-popup {
    .basic-popup .dx-overlay-content {
        border: 1px solid var(--blue-2);
        border-radius: 16px;
        box-shadow: 4px 4px 12px var(--shadow-1);

        .dx-toolbar.dx-popup-title,
        .dx-toolbar.dx-popup-bottom {
            padding: 0 16px;
        }

        .dx-toolbar.dx-popup-title {
            border-bottom: none;

            .dx-toolbar-items-container {
                padding: 24px 16px;
                border-bottom: 2px solid var(--blue-2);
                height: 70px;
            }

            .dx-toolbar-item-content {
                padding: 0 0 0 16px;
                margin: 0;
                font-size: 20px;
                font-weight: 600;
                letter-spacing: var(--letter-2);
            }

            .dx-toolbar-after {

                .dx-button.dx-state-hover,
                .dx-button.dx-state-focused,
                .dx-button.dx-state-active {
                    background-color: transparent;

                    .dx-inkripple-wave {
                        display: none;
                    }
                }

                .dx-icon-close:before {
                    content: url('../../assets/icons/buttons/x-mark.svg');
                }
            }
        }

        .dx-toolbar.dx-popup-bottom .dx-toolbar-items-container {
            height: 100%;
            padding: 16px 0;
            border-top: 2px solid var(--blue-2);
        }

        .dx-popup-content {
            padding: 0;
        }
    }
}