:root {
    --dx-datagrid-background: var(--white);
    --dx-master-detail-row-background: #f0f5ff;
}

.dx-swatch-material-blue-dark {
    --dx-datagrid-background: #363640;
}

dx-data-grid {
    background-color: transparent;
    // padding-left: 1em !important;
    // padding-right: 1em !important;
    // border-radius: 3px;
    // padding-top: 1em !important;

    // .dx-datagrid-header-panel {
    //     padding-bottom: 0.3em;
    //     padding-right: 0 !important;
    // }

    .dx-command-edit.dx-command-edit-with-icons {

        .dx-button-mode-contained,
        .dx-button-mode-contained.dx-state-hover {
            // box-shadow: 0 1px 3px rgba(0, 0, 0, .24) !important;
            box-shadow: none;
        }
    }
}

.basic-data-grid-table {
    padding-left: 8px !important;
    padding-right: 8px !important;

    @import './mixin/basic-grid.scss';
    @include datagrid-header-panel;
    @include datagrid-headers;
    @include datagrid-rowsview;

    &~app-notification-pager .pager {
        // margin-top: 16px;
        padding: 0 8px;
    }
}

.schedule-plan-data-grid {
    padding: 16px 24px !important;

    .dx-datagrid.dx-gridbase-container {
        padding: 0 4px;
    }

    .dx-datagrid-header-panel,
    .dx-datagrid-headers.dx-datagrid-nowrap.dx-bordered-top-view {
        border-bottom: 0;
        padding: 0;

        td {
            padding: 10px 0;
            border-bottom: 0;

            &:hover:not(.dx-command-select):not(.dx-command-expand):not(.dx-editor-cell):not(.dx-command-edit):not(.dx-datagrid-group-space) {
                background-color: transparent;
                cursor: default;
            }
        }
    }

    .dx-datagrid-rowsview td {
        padding: 8px 0;
    }

    &~app-notification-pager .pager {
        margin: 8px 0;
        padding: 0 24px;
    }
}



@import './mixin/jb-buttons.scss';
@import './mixin/jb-control-items.scss';

.jb-data-grid-container {
    height: 100%;
    padding: 8px 16px;
    border-radius: 16px;
    background-color: var(--white-1);

    display: flex;
    flex-direction: column;

    .jb-data-grid-toolbar {
        padding: 8px;

        &-first {
            display: flex;
            padding: 8px 0;
        }

        &-item {
            display: flex;
            gap: 8px;
        }

        &-empty {
            flex: 1
        }

        @include jb-toolbar-button;
        @include jb-toolbar-general-filter;
    }

    .jb-data-grid {
        padding: 0 4px;

        // header panel
        .dx-datagrid-header-panel {
            display: none;
        }

        // caption hover
        .dx-datagrid-headers .dx-datagrid-table .dx-row>td:hover:not(.dx-command-select):not(.dx-command-expand):not(.dx-editor-cell):not(.dx-command-edit):not(.dx-datagrid-group-space) {
            background-color: transparent;
            cursor: default;

            .dx-datagrid-text-content {
                color: inherit;
            }
        }

        // sorting
        .dx-sort {
            display: none !important;
        }

        // header
        .dx-datagrid-headers.dx-bordered-top-view {
            border-bottom: 0;
        }

        // data grid header 
        .dx-datagrid-headers .dx-datagrid-table .dx-header-row>td {
            // padding: 9.5px 0 9.5px 8px;
            padding: 9.5px 8px;
            border-bottom: 0;

            &.dx-pointer-events-none {
                border-right: 0;
            }
        }

        // data grid row 
        .dx-datagrid-rowsview .dx-datagrid-table .dx-data-row>td {
            // padding: 7.5px 0 7.5px 8px;
            padding: 7.5px 8px;
            border-bottom: 1px solid var(--gray-4);
        }
    }
}



// sorting
.dx-sort {
    display: none !important;
}

.custom-grid-container {
    height: 100%;
    display: flex;
    flex-direction: column;

    > app-tenant-chooser {
        flex: 0 0 70px;
    }

    > .grid-tool-bar {
        flex: 0 0 40px;
    }

    > dx-data-grid {
        overflow: hidden;
        flex: 1;
    }

    > app-notification-pager {
        flex: 0 0 36px;
        margin-top: 16px;
    }
}