@mixin datagrid-header-panel {
    .dx-datagrid-header-panel {
        border-bottom: none;
        // padding: 16px 8px;
        // margin-bottom: 10px;
    }
}

@mixin datagrid-headers {
    .dx-datagrid-headers.dx-bordered-top-view {
        background-color: transparent;
        border-bottom: none;

        .dx-datagrid-content {
            table.dx-datagrid-table {
                border-collapse: separate;

                colgroup .dx-col-fixed {
                    background-color: transparent;
                }

                .dx-row.dx-datagrid-filter-row {
                    background-color: transparent;

                    &>td {
                        border-bottom: none;

                        .basic-filter-row {
                            padding-left: 8px;
                            position: relative;

                            &-caption {
                                width: fit-content;
                                display: flex;
                                gap: 16px;
                                position: relative;
                            }

                            &-sorting {
                                display: flex;
                                gap: 4px;

                                &--inactive {
                                    opacity: 0;
                                    transform: translate(50%, 100%);
                                    pointer-events: none;
                                }

                                &-title {
                                    color: var(--gray-2);
                                }

                                &-icon {
                                    content: url('../../assets/icons/buttons/arrow-up.svg');

                                    &--down {
                                        transform: rotate(180deg);
                                    }
                                }
                            }

                            &-search {
                                width: 16px;
                                height: 16px;

                                &-icon {
                                    content: url('../../assets/icons/buttons/search-icon.svg');
                                    cursor: pointer;
                                    position: absolute;
                                    right: 0;
                                    transition: right 0.4s linear;

                                    &--active {
                                        right: calc(100% - 16px);
                                    }
                                }
                            }

                            &-textbox {
                                position: absolute;
                                top: 50%;
                                left: calc(8px + 16px + 8px);
                                opacity: 0;
                                transform: translate(50%, 100%);

                                &--active {
                                    opacity: 1;
                                    transform: translate(0, -50%);
                                }

                                .dx-texteditor-input-container {
                                    .dx-texteditor-input {
                                        padding: 0;
                                    }

                                    .dx-placeholder {
                                        color: var(--gray-3);

                                        &::before {
                                            padding: 0;
                                        }
                                    }
                                }
                            }

                            &-datebox {
                                position: absolute;
                                top: 50%;
                                left: calc(8px + 16px + 8px);
                                opacity: 0;
                                transform: translate(50%, 100%);

                                &--active {
                                    opacity: 1;
                                    transform: translate(0, -50%);
                                }

                                .dx-texteditor-input-container {
                                    .dx-texteditor-input {
                                        padding: 0;
                                    }

                                    .dx-placeholder {
                                        color: var(--gray-3);

                                        &::before {
                                            padding: 0;
                                        }
                                    }
                                }
                            }

                            &-sorting,
                            &-textbox,
                            &-datebox {
                                transition: transform 0.3s linear, opacity 0.6s linear;
                            }
                        }

                        &:first-child .basic-filter-row {
                            padding-left: 16px;

                            &-textbox {
                                left: calc(16px + 16px + 8px);
                            }

                            &-datebox {
                                left: calc(16px + 16px + 8px);
                            }
                        }
                    }
                }

                // headerhoversorting.mode = 'none'
                .dx-row.dx-header-row>td {
                    padding-left: 8px;
                    padding-right: 0;

                    &:hover:not(.dx-command-select):not(.dx-command-expand):not(.dx-editor-cell):not(.dx-command-edit):not(.dx-datagrid-group-space) {
                        background-color: transparent;
                        cursor: default;
                    }
                }
            }

            .dx-datagrid-focus-overlay {
                display: none;
            }
        }

        // headerz-indexheader
        .dx-datagrid-scrollable-simulated.dx-datagrid-content.dx-datagrid-scroll-container:not(.dx-datagrid-content-fixed) {
            // position: relative;
            // z-index: 3;
        }

        // header
        .dx-datagrid-scrollable-simulated.dx-datagrid-content.dx-datagrid-scroll-container.dx-datagrid-content-fixed td:not(.dx-pointer-events-none.dx-first-cell) {
            background-color: #F7F5E7;
            cursor: default;
        }
    }
}

@mixin datagrid-rowsview {
    .dx-bordered-bottom-view.dx-datagrid-rowsview {
        background-color: transparent;
        margin-top: 16px;

        .dx-datagrid-content table.dx-datagrid-table {
            border-collapse: separate;
            border-spacing: 0 12px;
            margin-top: -12px;

            .dx-row.dx-data-row {
                background-color: var(--white-1);

                &>td {
                    border-top: 1px solid var(--gray-2);
                    border-bottom: 1px solid var(--gray-2);
                    padding: 8px;

                    &:first-child {
                        border-left: 1px solid var(--gray-2);
                        border-top-left-radius: 5px;
                        border-bottom-left-radius: 5px;
                        padding-left: 16px;
                    }

                    &:last-child {
                        border-right: 1px solid var(--gray-2);
                        border-top-right-radius: 5px;
                        border-bottom-right-radius: 5px;
                        padding-right: 16px;
                    }

                    &.dx-command-expand.dx-datagrid-group-space {
                        padding-left: 0;

                        .dx-datagrid-group-closed,
                        .dx-datagrid-group-opened {
                            //  32 + padding 16 + boder 1 = 49
                            // left17pxpadding + border17px
                            width: 49px;
                            height: 32px;
                            position: relative;
                            left: -17px;

                            &::before {
                                width: 100%;
                                height: 100%;
                            }
                        }
                    }
                }

                &.master-detail-row-collapse>td:first-child {
                    border-bottom-left-radius: 0;
                }

                &>td.dx-command-expand.dx-datagrid-group-space[aria-label="Collapse"] {
                    border-bottom-right-radius: 0;
                }
            }

            // master detailz-index
            .dx-row.dx-master-detail-row {
                position: relative;
                z-index: 1;

                &>td {
                    border: 1px solid var(--gray-2);
                    border-top: none;
                    border-bottom-left-radius: 5px;
                    border-bottom-right-radius: 5px;
                    background-color: var(--white-1);
                    padding: 0;

                    &::before {
                        content: '';
                        position: absolute;
                        top: -13px;
                        left: 0;
                        z-index: -1;
                        width: 100%;
                        height: 100%;
                        background-color: var(--dx-master-detail-row-background);
                        border-left: 1px solid var(--gray-2);
                        border-right: 1px solid var(--gray-2);
                    }
                }
            }

            .dx-row.dx-freespace-row {

                .dx-command-edit.dx-command-edit-with-icons,
                .dx-command-expand.dx-datagrid-group-space {
                    display: none;
                }

                // selectionrowdata grid
                // 
                .dx-command-select {
                    background-color: var(--lpx-content-background);
                }
            }

            &.dx-pointer-events-none .dx-row.dx-data-row {
                .dx-command-edit {}

                .dx-command-expand {}
            }
        }
    }
}

@mixin datagrid-table-button {
    .basic-data-grid-table {
        .dx-row.dx-data-row>td.dx-command-expand.dx-datagrid-group-space {
            .dx-datagrid-group-closed::before {
                content: url('../../assets/icons/buttons/chevron-down.svg');
            }

            .dx-datagrid-group-opened::before {
                content: url('../../assets/icons/buttons/chevron-up.svg');
            }
        }
    }
}

@mixin datagrid-row-button {
    .basic-data-grid-button.dx-widget.dx-button {
        border-radius: 0;
        border: none;
        min-width: 24px;
        height: 24px;

        .dx-button-content {
            padding: 0;

            &::before {
                width: 100%;
                height: 100%;
            }
        }

        &.dx-state-hover {
            background-color: transparent;
        }

        &.basic-data-grid-button--add {
            width: 40px;
            height: 40px;

            .dx-button-content::before {
                content: url('../../assets/icons/buttons/add-icon.svg');
            }
        }

        &.basic-data-grid-button--detail {
            .dx-button-content::before {
                content: url('../../assets/icons/buttons/detail-icon.svg');
            }

            &.dx-state-hover {
                .dx-button-content::before {
                    content: url('../../assets/icons/buttons/detail-icon-hover.svg');
                }
            }
        }

        &.basic-data-grid-button--edit {
            .dx-button-content::before {
                content: url('../../assets/icons/buttons/edit-icon.svg');
            }

            &.dx-state-hover {
                .dx-button-content::before {
                    content: url('../../assets/icons/buttons/edit-icon-hover.svg');
                }
            }
        }

        &.basic-data-grid-button--delete {
            .dx-button-content::before {
                content: url('../../assets/icons/buttons/delete-icon.svg');
            }

            &.dx-state-hover {
                .dx-button-content::before {
                    content: url('../../assets/icons/buttons/delete-icon-hover.svg');
                }
            }
        }

        &.basic-data-grid-button--repush {
            .dx-button-content::before {
                content: url('../../assets/icons/buttons/repush-icon.svg');
            }
        }
    }
}